<template>
    <div class="task-grid">
        <template v-if="tasks.length">
            <Task v-for="(task, i) in tasks" :key="task.name" :task="task"
                @taskDeleted="$emit('taskDeleted', i)"
                @taskStateChanged="$emit('taskStateChanged', i)"/>
        </template>
        <p v-else class="no-task">No pending tasks :)</p>
    </div>
</template>

<script>
// Component imports
import Task from './Task.vue'

// Vue definition
export default {
    components: { Task },
    props: {
        tasks: { type: Array, required: true }
    }
}
</script>

<style>
    .task-grid {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
    }
    .task-grid .task {
        margin: 10px;
    }
    .no-task {
        color: #AAA;
        font-size: 1.7rem;
    }
</style>
