<template>
    <div class="task-new">
        <input v-model="name" @keydown.enter="add" type="text"
            placeholder="What is your task?" class="form-element" >
        <button class="form-element" @click="add">+</button>
    </div>
</template>

<script>
export default {
    data() {
        return {
            name: ''
        }
    },
    methods: {
        add() {
            this.$emit('taskAdded', { name: this.name })
            this.name = '';
        }
    }
}
</script>

<style>
    .task-new {
        margin: 35px;
    }
    .form-element {
        outline: none;
        font-size: 2rem;
        border: 1px solid #fff;
        padding: 5px 10px 8px;
        color: #fff;
    }
    input.form-element {
        width: 500px;
        background: #fff2;
        border-top-left-radius: 8px;
        border-bottom-left-radius: 8px;
    }
    button.form-element {
        border-left: none;
        background-color: #FFF2AA;
        border-top-right-radius: 8px;
        border-bottom-right-radius: 8px;
        color: #424242;
    }
</style>
